import React, { useState, useEffect } from 'react';
import { auth, storage, firestore } from '../firebase';
import { doc, getDoc, updateDoc, deleteField, collection, query, where, getDocs } from 'firebase/firestore';
import { updateProfile } from 'firebase/auth';
import { ref, uploadBytesResumable, getDownloadURL, deleteObject } from 'firebase/storage';
import { useParams } from 'react-router-dom';

function ExpertPage() {
    const { customURL } = useParams();
    const [user, setUser] = useState(null);
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [photoURL, setPhotoURL] = useState(null);
    const [previousPhotoRef, setPreviousPhotoRef] = useState(null);
    const [message, setMessage] = useState({ text: '', type: '' });
    const [updateMessage, setUpdateMessage] = useState({ text: '', type: '' });
    const [imageLoadError, setImageLoadError] = useState(false);
    const [address, setAddress] = useState('');
    const [city, setCity] = useState('');
    const [county, setCounty] = useState('');
    const [postcode, setPostcode] = useState('');
    const [mobileNumber, setMobileNumber] = useState('');
    const [role, setRole] = useState('');
    const [email, setEmail] = useState(''); 
    const [description, setDescription] = useState('');

    


  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (authUser) => {
      if (authUser) {
        setUser(authUser);

        const userDocRef = doc(firestore, 'expertApplication', authUser.uid);
        const userDocSnapshot = await getDoc(userDocRef);
        if (userDocSnapshot.exists()) {
          const userData = userDocSnapshot.data();
          if (userData.photoURL) {
            setPhotoURL(userData.photoURL);
          } else {
            setPhotoURL(authUser.photoURL);
          }
          setFirstName(userData.firstName || authUser.displayName.split(' ')[0] || '');
          setLastName(userData.lastName || authUser.displayName.split(' ')[1] || '');
          setAddress(userData.address || '');
          setCity(userData.city || '');
          setCounty(userData.county || '');
          setPostcode(userData.postcode || '');
          setMobileNumber(userData.mobileNumber || '');
          setRole(userData.role || '');
          setDescription(userData.description || '');
          setEmail(authUser.email); // Set email from authUser
        } else {
          setFirstName(authUser.displayName.split(' ')[0] || '');
          setLastName(authUser.displayName.split(' ')[1] || '');
          setPhotoURL(authUser.photoURL);
          setEmail(authUser.email); // Set email from authUser
        }
      } else {
        setAddress('');
        setCity('');
        setCounty('');
        setPostcode('');
        setMobileNumber('');
        setRole('');
        setPhotoURL(null);
        setEmail(''); // Clear email
        setDescription('');
      }
    }, []);

    const fetchUserData = async () => {
      try {
        if (!customURL) {
          // Handle the case where customURL is not defined
          console.log('Custom URL is not defined.');
          return;
        }
    
        // Query the userProfiles collection based on customURL
        console.log('Custom URL:', customURL);
        const userProfileQuery = query(
          collection(firestore, 'expertApplications'),
          where('customURL', '==', customURL)
        );
    
        const userProfileQuerySnapshot = await getDocs(userProfileQuery);
    
        if (!userProfileQuerySnapshot.empty) {
          const userData = userProfileQuerySnapshot.docs[0].data();
          // Update the state with user data
          setFirstName(userData.displayName.split(' ')[0] || '');
          setLastName(userData.displayName.split(' ')[1] || '');
          setAddress(userData.address || '');
          setCity(userData.city || '');
          setCounty(userData.county || '');
          setPostcode(userData.postcode || '');
          setMobileNumber(userData.mobileNumber || '');
          setRole(userData.role || '');
          setDescription(userData.description || '');
          setEmail(userData.email || ''); // Set email from userData
          setPhotoURL(userData.photoURL || null); // Set photoURL from userData
        } else {
          console.log('Custom URL not found:', customURL);
        }
      } catch (error) {
        // Handle errors
        console.error('Error fetching user data:', error);
      }
    };
    
    // Call the function to fetch user data based on customURL
    if (customURL) {
      fetchUserData();
    }
    
    return () => {
      unsubscribe();
    };
    
  }, [customURL]);




  return (
    <div className="profile-container">
      <div className="profile-intro-section">
        <div className="container">
          <div className="row align-items-center text-white">

              <div className="welcome-text">
                <h1 className="email-h3"> Page of
                <span className="email-h3"> {`${firstName} ${lastName}`}</span> 
                </h1>
              </div>
       
    
          </div>
        </div>
      </div>
      <div className="profile-section">
      <div className="content profile-content ">
          <div className="row">
            <div className="my-4">
              <h3>Profile of <span> </span>
              {`${firstName} ${lastName}`} 


              </h3>
              <hr />
            </div>
            <form className="file-upload">
              <div className="row mb-4 gx-4">
                <div className="col-xl-8 col-12">
                  <div className="bg-white shadow-sm mb-4 card border-light">
                    <div className="card-body card-profile">
                      <div className="row g-3">
                      <h4 className="mb-4">General information</h4>
    <div className="col-md-6">
    <label className="form-label">First Name *</label>
    <input
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    aria-label="First name"
    value={firstName} // Display user's first name here
    onChange={(e) => setFirstName(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">Last Name *</label>
    <input
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    aria-label="Last name"
    value={lastName} // Display user's last name here
    onChange={(e) => setLastName(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label htmlFor="inputEmail4" className="form-label">Email *</label>
    <input
    type="email"
    className="form-control form-control-lg"
    id="inputEmail4"
    value={user ? user.email : ''} // Display user's email here
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">Mobile number *</label>
    <input
    id="inputMobileNumber"
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    aria-label="Phone number"
    value={mobileNumber} // Display user's mobile number here
    readOnly // Make it read-only
    onChange={(e) => setMobileNumber(e.target.value)}
      style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-12">
    <label className="form-label">Role/Expertise *</label>
    <input
    id="inputRole"
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    aria-label="your Role"
    value={role} 
    onChange={(e) => setRole(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-12">
    <label className="form-label">Description of Expertise</label>
    <textarea
    id="inputDescription"
    type="text"
    className="form-control"
    rows="4"
    placeholder=""
    value={description} // Create a 'description' state and update it accordingly
    readOnly // Make it read-only
    onChange={(e) => setDescription(e.target.value)}
    style={{ resize: 'none' , pointerEvents: 'none' }}
    
    />
    </div>
    </div>
    <div className="py-5 rounded mt-4">
    <div className="row g-3">
    <h4 className="mb-4 mt-0">Address</h4>
    <div className="col-md-6">
    <label className="form-label">Address</label>
    <input
    id="inputAddress"
    type="text"
    className="form-control form-control-lg"
    placeholder=""
    value={address} // Display user's address here
    onChange={(e) => setAddress(e.target.value)}
    style={{ pointerEvents: 'none' }}
    readOnly // Make it read-only
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">City</label>
    <input
    id="inputCity"
    type="text"
    className="form-control form-control-lg"
    placeholder="Enter City"
    value={city} // Display user's city here
    onChange={(e) => setCity(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">County</label>
    <input
    id="inputCounty"
    type="text"
    className="form-control form-control-lg"
    placeholder="County"
    value={county} // Display user's county here
    onChange={(e) => setCounty(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    <div className="col-md-6">
    <label className="form-label">Postcode</label>
    <input
    id="inputPostcode"
    type="text"
    className="form-control form-control-lg"
    placeholder="Postcode"
    value={postcode} // Display user's postcode here
    onChange={(e) => setPostcode(e.target.value)}
    readOnly // Make it read-only
    style={{ pointerEvents: 'none' }}
    />
    </div>
    
    </div>
    </div>
    </div>
    </div>
    </div>
                    

<div className="col-xl-4 col-12">
<div className="bg-white shadow-sm mb-4 card border-light">
<div className="card-profile">
<div className="profile-cover rounded-top">
</div>
<div className="row g-3 ">
<div className="text-center">
<div className="pb-5 card-body">
{/* Center the image using Bootstrap's flexbox classes */}
{!imageLoadError && photoURL && (
  <img
    src={photoURL}
    alt="User Profile"
    className="card-img user-avatar large-avatar rounded-circle mx-auto mt-n7 mb-5"
    onError={() => setImageLoadError(true)}
    onChange={(e) => setPhotoURL(e.target.value)}
  />
)}

{imageLoadError && (
  <img
    src={`${process.env.PUBLIC_URL}/images/arts/user.webp`}
    alt="Custom 404 Image"
    className="card-img user-avatar large-avatar rounded-circle mx-auto mt-n7 mb-5"
    onChange={(e) => setPhotoURL(e.target.value)}
  />
)}

<div>
{message.type === 'error' && (
<p className="fw-light" style={{ color: 'red' }}>{message.text}</p>
)}
{message.type === 'success' && (
<p className="fw-light" style={{ color: 'green' }}>{message.text}</p>
)}
{message.type === 'progress' && (
<p className="fw-light" style={{ color: 'gray' }}>{message.text}</p>
)}
</div>
<div className="card-title h5">{`${firstName} ${lastName}`} </div>
<div className="fw-normal card-subtitle mb-4 h6">{role}</div>
<button type="button" className="me-4 btn btn-primary btn-sm rounded-pill pt-2 pb-2">
<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
<path d="M6.25 6.375a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0zM3.25 19.125a7.125 7.125 0 0114.25 0v.003l-.001.119a.75.75 0 01-.363.63 13.067 13.067 0 01-6.761 1.873c-2.472 0-4.786-.684-6.76-1.873a.75.75 0 01-.364-.63l-.001-.122zM19.75 7.5a.75.75 0 00-1.5 0v2.25H16a.75.75 0 000 1.5h2.25v2.25a.75.75 0 001.5 0v-2.25H22a.75.75 0 000-1.5h-2.25V7.5z" />
</svg>
Message
</button>
<button type="button" className="btn btn-secondary btn-sm rounded-pill pt-2 pb-2">
<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
<path fillRule="evenodd" d="M4.804 21.644A6.707 6.707 0 006 21.75a6.721 6.721 0 003.583-1.029c.774.182 1.584.279 2.417.279 5.322 0 9.75-3.97 9.75-9 0-5.03-4.428-9-9.75-9s-9.75 3.97-9.75 9c0 2.409 1.025 4.587 2.674 6.192.232.226.277.428.254.543a3.73 3.73 0 01-.814 1.686.75.75 0 00.44 1.223zM8.25 10.875a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25zM10.875 12a1.125 1.125 0 112.25 0 1.125 1.125 0 01-2.25 0zm4.875-1.125a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25z" clipRule="evenodd" />
</svg>
Connect
</button>

</div>
</div>
</div>
</div>
</div>


</div>
</div>



</form>
</div>
</div>
</div>
</div>
);
}

export default ExpertPage;
